*{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.container{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(#121212, #212b46);
}

.title{
  font-size: 85px;
  color: #fff;
  -webkit-animation: flipTitle 0.8s;
          animation: flipTitle 0.8s;
}

@-webkit-keyframes flipTitle {
  from{
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }
  to{
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}

@keyframes flipTitle {
  from{
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }
  to{
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}

.containerInput{
  background-color: rgba(255,255,255, 0.2);
  padding: 15px;
  margin: 34px 0;
  display: flex;
  border-radius: 8px;
  box-shadow: 1px 3px 8px rgba(0,0,0, 0.5);
}

.containerInput input{
  background-color: transparent;
  border: 0;
  font-size: 20px;
  color: #fff;
  outline: none;
  margin-right: 8px;
}

.containerInput input::-webkit-input-placeholder{
  color: #f1f1f1
}

.containerInput input:-ms-input-placeholder{
  color: #f1f1f1
}

.containerInput input::placeholder{
  color: #f1f1f1
}

.buttonSearch{
  background-color: transparent;
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.buttonSearch:hover{
  -webkit-transform: scale(1.2);
          transform: scale(1.2)
}

.main{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #fff;
  width: 500px;
  border-radius: 8px;
}

.main h2{
  margin: 16px 0 ;
  font-size: 39px;
}
.main span{
  margin-bottom: 16px;
  font-weight: bold;
}

.avisoNumeros{
  color: rgba(255,255,255,0.3);
  margin-top: -25px;
  margin-bottom: 20px;
  justify-content: right;
}

@media (max-width: 620px) {
  .title{
    font-size: 60px;
  }
  .main h2{
    font-size: 28px;
  }

  .main{
    width: 80%;
  }
}
